
html, body {
    overflow-y: visible;
}
.el-row {
    margin-bottom: 20px;
}
.el-col {
    border-radius: 4px;
}
.bg-purple-dark {
    background: #99a9bf;
}
.bg-purple {
    background: #d3dce6;
}
.bg-purple-light {
    background: #e5e9f2;
}
.grid-content {
    border-radius: 4px;
    min-height: 36px;
}
.row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
}
.bm-0 {
    margin-bottom: 0 !important;
}
.bm-5 {
    margin-bottom: 5px !important;
}
.tm-0 {
    margin-top: 0 !important;
}
.tm-10 {
    margin-top: 10px !important;
}
.lm-0 {
    margin-left: 0 !important;
}
.rm-0 {
    margin-right: 0 !important;
}
.m-0 {
    margin: 0 !important;
}
.c-s .el-form-item__label {
    font-size: inherit !important;
    padding: 0 !important;
}
.red--text {
    color: #FF4949 !important;
}
.t-monitor {
    -webkit-transform: scale(1, -1);
            transform: scale(1, -1);
}
.csp-radio-btn-small .el-radio-button__inner {
    padding: 3px;
}
.csp-error {
    border-color: #ff4949;
}



